import React, { Component } from 'react';

import axios from 'axios';

import logo from './logo.svg';
import './App.css';

import Panel from 'react-bootstrap/lib/Panel';


class App extends Component {
  constructor() {
    super()

    this.state = {
      url: '',
      text: false
    }

    this._changeUrl = this._changeUrl.bind(this)
    this._sendRequest = this._sendRequest.bind(this)
  }

  _changeUrl(e) {
    this.setState({
      url: e.target.value
    })
  }

  _sendRequest() {
    const url = this.state.url
    axios.get(`http://${url}`, { timeout: 3000 }).then((res) => {
      if (res.status === 200) {
        this.setState({
          text: 'Request has been correctly received from the server'
        })
      } else {
        this.setState({
          text: 'There was an error sending the request to the server'
        })
      }
    }).catch((e) => {
      this.setState({
        text: 'There was an error sending the request to the server'
      })
    })
  } 

  render() {
    return (
        <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Cloud Academy</h1>
        </header>
        <Panel bsStyle="info" className="centeralign">
          <Panel.Heading>
            <Panel.Title componentClass="h3">Request Form</Panel.Title>
          </Panel.Heading>
          <Panel.Body>
            <input
              type="text"
              placeholder="Enter IP:port here"
              onChange={ this._changeUrl } 
            />
            <hr/>
            <button onClick={ this._sendRequest }>Send Request</button>
            <hr />
            {this.state.text ? <span> {this.state.text} </span> : <span></span>}
          </Panel.Body>
      </Panel>
      </div>
    );
  }
}

export default App;



